<template>
  <el-main>
    <h3 style="padding-bottom: 5px;border-bottom: 1px solid #eee">企业名称：中国平安</h3>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人姓名：</i></label>
      <el-input class="input-width" v-model="servesInfo.userName" placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人性别：</i></label>
      <el-select v-model="servesInfo.sex" placeholder="请选择">
        <el-option
          v-for="item in sexList"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人证件类型：</i></label>
      <el-select v-model="servesInfo.paperType" placeholder="全部">
        <el-option
          v-for="item in idTypeList"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人证件号码：</i></label>
      <el-input class="input-width" v-model="servesInfo.paper" placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人电话号码：</i></label>
      <el-input class="input-width" v-model="servesInfo.mobile" placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label for="" class="list-item"><i>被服务人电子邮件：</i></label>
      <el-input class="input-width" v-model="servesInfo.email"  placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>所属商品：</i></label>
      <el-select v-model="value" placeholder="全部">
        <el-option
          v-for="item in goodPlanList"
          :key="item.value"
          :label="item.name"
          :value="item.goodsName">
        </el-option>
      </el-select>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>保单号/服务单号：</i></label>
      <el-input class="input-width" v-model="servesInfo.cardNum" placeholder="请输入内容"></el-input>
    </div>
    <div class="list"> 
        <label for="" class="list-item require"><i>商品生效日期：</i></label>
        <el-date-picker v-model="servesInfo.beginTime" type="date" placeholder="选择日期" />
        
        <label for="" class="list-item require"><i>商品失效日期：</i></label>
        <el-date-picker v-model="servesInfo.endTime" type="date" placeholder="选择日期" />
      </div>
    <div class="list">
      <label class="list-item require"><i>特别约定：</i></label>
        <el-input
          type="textarea"
          :rows="2"
          size="medium"
          placeholder="请输入内容"
          v-model="servesInfo.remark">
        </el-input>
    </div>
    <div class="list">
      <label class="list-item textarea-label"><i>保单状态：</i></label>
      <div class="list-radio">
        <el-radio v-model="servesInfo.status" label="1">正常</el-radio>
        <el-radio v-model="servesInfo.status" label="2">中止</el-radio>
        <el-radio v-model="servesInfo.status" label="3">终止</el-radio>
        <el-radio v-model="servesInfo.status" label="4">失效</el-radio>
      </div>
    </div>
    <div class="list btn-center">
      <el-button type="primary" @click='addOrderRelation'>增加关联权益人</el-button>
    </div>
    <div class="other-info" v-show="otherInfo">
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人姓名：</i></label>
        <el-input class="input-width" v-model="comName" placeholder="请输入内容"></el-input>
      </div>
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人性别：</i></label>
        <el-select v-model="value" placeholder="全部">
          <el-option
            v-for="item in sexList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人证件类型：</i></label>
        <el-select v-model="value" placeholder="全部">
          <el-option
            v-for="item in idTypeList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人证件号码：</i></label>
        <el-input class="input-width" v-model="comName" placeholder="请输入内容"></el-input>
    </div>
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人证件号码：</i></label>
        <el-input class="input-width" v-model="comName" placeholder="请输入内容"></el-input>
    </div>
      <div class="list">
        <label for="" class="list-item"><i>关联权益人电子邮件：</i></label>
        <el-input class="input-width" v-model="comName" placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label class="list-item textarea-label"><i>与被服务人关系：</i></label>
      <div class="list-radio">
        <el-radio v-model="radio2" label="1">父母</el-radio>
        <el-radio v-model="radio2" label="2">子女</el-radio>
        <el-radio v-model="radio2" label="3">配偶</el-radio>
        <el-radio v-model="radio2" label="4">其他</el-radio>
      </div>
    </div>
    </div>
    <div class="btn-center">
      <el-button type="primary" @click='confirm'>提交</el-button>
      <el-button type="" @click='cancel'>取消</el-button>
    </div>
  </el-main>
</template>

<script>
const queryString = require('querystring')
import { sexList, idTypeList, paperTypeList } from 'data/servesList'
export default {
  name: 'addOrEditServe',
  data() {
    return {
      isAdd: this.$route.query.type === 'add',
      id: '22332',
      comName: '',
      sexList,
      idTypeList,
      value: '',
      idNumber: '',
      phoneNumber: '',
      email: '',
      goodPlanList: [],
      insureNumber: '',
      availTime: '',
      expiryTime: '',
      otherInfo: false,
      radio1: '',
      radio2: '',
      textarea: '',
      servesInfo: {}
    }
  },
  methods: {
    addOrderRelation() {
      this.otherInfo = true;
    },
    confirm() {
      const type = this.isAdd ? 'post' : 'update'
      this.axios[type]('/order', queryString.stringify(this.servesInfo)).then((res) => {
        const code = Number(res.data.code)
        if (code === 200) {
          this.$message(this.mes.success)
        } else {
          this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    },
    cancel() {
      this.$router.push('servesList')
    },
    getGoodsPlan() {
      const param = {
        companyId: this.id
      }
      this.axios.get('/goodsPlan?' +  queryString.stringify(param)).then(res => {
        const code = Number(res.data.code)
        if (code === 200) {
          this.goodPlanList = res.data.data
        } else {
          this.$message(this.mes.busy)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import url('../../../assets/less/input.less');
.btn-center{
  display: flex;
  justify-content: center;
}
</style>
